// Colors

// Default
$color-default-black: #000000;
$color-default-white: #ffffff;
$color-transparent: rgba(255, 255, 255, 0);

// Project palette

$color-main-dark: #484848;

$color-primary: #316dc2;
$color-primary-darker: #275699;
$color-light: #f0efe9;
$color-disabled: #a9afb7;
$color-error: #ff5e66;

$modal-overlay: rgba(13, 29, 51, 0.4);
$picture-overlay: rgba(49, 109, 194, 0.2);

// Gradient


// Typography
$fz-default: 16px;
$ff-manrope: "Manrope", "Arial", sans-serif;

// Animation
$tf-default: ease-in;
$trans-default: 0.3s $tf-default;

//      $tf-default,     
$trans-modal: 0.6s cubic-bezier(0.55, 0, 0.1, 1);
$trans-600: 0.6s $tf-default;

// Viewports
$vp-320: 320px;
$vp-768: 768px;
$vp-1199: 1199px;
$vp-1200: 1200px;
$vp-1440: 1440px;

// Retina
$retina-dpi: 144dpi;
$retina-dppx: 1.5dppx;
